li.line-music {
  font-size: 18px;
  display: inline;
}
ul.music-table {
  position: absolute;
  left: 26%;
  top: 304px;
  width: 1000px;
  height: 515px;
  background: rgba(194, 214, 228, 0.26);
  border-radius: 40px;
}

.music-p {
  padding-top: 1.3%;
  padding-left: 9%;
}

.musictitle {
  width: 100%;
  font-size: 30px;
  text-align: center;
  line-height: 300px;
}

p.react-player-licencee {
  position: absolute;
  top: 97%;
}
