li.line-score {
  font-size: 23px;
  display: inline;
}

ul.score-table {
  position: absolute;
  left: 26%;
  top: 354px;
  width: 1000px;
  height: 430px;
  background: rgba(103, 146, 175, 0.26);
  border-radius: 90px;
}

.scoretitle {
  position: relative;
  font-size: 34px;
  text-align: center;
  line-height: 340px;
  padding-top: 1%;
}
.score-span {
  padding-left: 2%;
}
.score-p {
  padding-top: 1.3%;
  padding-left: 9%;
}

p.timeago-license {
  position: absolute;
  top: 97%;
}

